    getProduct = (ByteText, startIndex) => {
        var i2 = this.getIndex(ByteText, "puid", 2)
        this.setState({ ResumeFrom: i2 })
        var proinvalid = ByteText.substring(startIndex - 9, i2)
        console.log(proinvalid)
        var product = proinvalid.substring(0, proinvalid.length - 9)
        var ProJsn = JSON.parse(product)
        console.log(ProJsn)
        return ProJsn
    }

    getIndex(string, subString, index) {
        return string.split(subString, index).join(subString).length;
    }

